/**
 * @see https://gist.github.com/alexbruno/6623b5afa847f891de9cb6f704d86d02
 */

// Regex para validao de string no formato CNPJ
export const regexCNPJ = /^\d{2}.\d{3}.\d{3}\/\d{4}-\d{2}$/;

// Mtodo de validao
// Referncia: https://pt.wikipedia.org/wiki/Cadastro_Nacional_da_Pessoa_Jur%C3%ADdica
export function validCNPJ(value: string | number | number[] = "") {
  if (!value) return false;

  // Aceita receber o valor como string, nmero ou array com todos os dgitos
  const isString = typeof value === "string";
  const validTypes =
    isString || Number.isInteger(value) || Array.isArray(value);

  // Elimina valor de tipo invlido
  if (!validTypes) return false;

  // Filtro inicial para entradas do tipo string
  if (isString) {
    // Teste Regex para veificar se  uma string apenas dgitos vlida
    const digitsOnly = /^\d{14}$/.test(value);
    // Teste Regex para verificar se  uma string formatada vlida
    const validFormat = regexCNPJ.test(value);
    // Verifica se o valor passou em ao menos 1 dos testes
    const isValid = digitsOnly || validFormat;

    // Se o formato no  vlido, retorna invlido
    if (!isValid) return false;
  }

  // Elimina tudo que no  dgito
  const numbers = matchNumbers(value);

  // Valida a quantidade de dgitos
  if (numbers.length !== 14) return false;

  // Elimina invlidos com todos os dgitos iguais
  const items = [...new Set(numbers)];
  if (items.length === 1) return false;

  // Separa os 2 ltimos dgitos verificadores
  const digits = numbers.slice(12);

  // Valida 1o. dgito verificador
  const digit0 = validCalc(12, numbers);
  if (digit0 !== digits[0]) return false;

  // Valida 2o. dgito verificador
  const digit1 = validCalc(13, numbers);
  return digit1 === digits[1];
}

// Mtodo de formatao
export function formatCNPJ(value: string | number | number[] = "") {
  // Verifica se o valor  vlido
  const valid = validCNPJ(value);

  // Se o valor no for vlido, retorna vazio
  if (!valid) return "";

  // Elimina tudo que no  dgito
  const numbers = matchNumbers(value);
  const text = numbers.join("");

  // Formatao do CNPJ: 99.999.999/9999-99
  // Retorna o valor formatado
  return text.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, "$1.$2.$3/$4-$5");
}

// Clculo validador
function validCalc(x: number, numbers: number[]) {
  const slice = numbers.slice(0, x);
  let factor = x - 7;
  let sum = 0;

  for (let i = x; i >= 1; i--) {
    const n = slice[x - i];
    sum += n * factor--;
    if (factor < 2) factor = 9;
  }

  const result = 11 - (sum % 11);

  return result > 9 ? 0 : result;
}

// Elimina tudo que no  dgito
function matchNumbers(value: string | number | number[] = "") {
  const match = value.toString().match(/\d/g);
  return Array.isArray(match) ? match.map(Number) : [];
}
